
@use '../../abstracts' as *;
@use '../../base' as *;
@use '../../layout' as *;

.global-footer {
    background: $color-nearblack;
    color: $color-purewhite;
    margin-left: 0;
    margin-right: 0;
    padding-left: 0;
    padding-right: 0;
    padding-top: $measure--medium3;

    @include media('>=small') {
        @include standard-box();
        @include x-column-grid(24);
    }
  
}

.global-footer__container {
    padding-bottom: $measure--small4;
    @include default-padding();
}

.global-footer__header-logo {
    color: $color-purewhite;
    font-weight: $font-weight-book;
    letter-spacing: -0.5px;
    font-size: $measure--medium1;
    font-family: "ff-meta-web",Arial,Helvetica,sans-serif;

    &:visited {
        color: $color-purewhite;
    }
    
    &:focus, &:active {
        //border: 1px dotted #999999;
    }

    span.global-footer__logo-style {
        font-weight: $font-weight-bold;
        letter-spacing: -0.5px;
    }
}

.global-footer__social-media {
    display: flex;
    align-items: self-end;
    justify-content: flex-end;
}

.global-footer__social-media__icons {
    display: flex;
    flex-direction: row;

    .global-footer__social-media__icon {
        margin-right: $measure--small2;

        &:last-of-type {
            margin-right: 0;
        }
    }
}

.global-footer__row {
    margin-top: $measure--basic;
    display: flex;
    column-gap: $measure--basic;

    &:nth-of-type(2)  {

        .global-footer__content {
            display: flex;
            width: 100%;            
        }

        .global-footer__content__text {
            flex-basis: 75%;

            p, ul {
                margin-bottom: $measure--basic;
            }
        }

        .global-footer__content__logo {
            flex-basis: 25%;
            display: flex;
            justify-content: flex-end;
            
        }

        .logo {
            height: 100%
        }

    }

}

.global-footer__useful-links {
    list-style-type: none;
    display: flex;
    flex-direction: row;

    li {
        border-right: thin solid white;
        margin-right: $measure--basic;
        padding-right: $measure--basic;
        &:last-of-type { 
            border-right: none;
        }
    }
}

.global-footer__content__text__copyright {
    font-weight: $font-weight-bold;
}

.global-footer__col {
    flex-basis: 25%;

    ul {
        list-style-type: none;
    }
    
    
}

hr {
    border-color: $color-ford-grey;
    margin-top: $measure--medium3;
}