@use '../../abstracts' as *;
@use '../../base' as *;
@use '../../layout' as *;

.global-header__wrap {
    width: 100%;
    position: relative;
    z-index: z-index('unveiled');
}

.global-header__ribbon {
    background-color: $color-red50;
    margin-bottom: $measure--small4;
    padding-top: $measure--basic;
    padding-bottom: $measure--basic;
    
}

.global-header__ribbon__container {
    @include media('>=small') {
        @include standard-box();
    }
}

.global-header__ribbon__message {
    display: flex;
    flex-direction: row;
    justify-content: center;
    @include default-padding();
}

.global-header__ribbon__close {
    margin-left: $measure--basic;
}

.global-header {
    margin-top: $measure--small4;
    

    @include media('>=small') {
        @include standard-box();
    }

    .navigation--primary {
        grid-column-start: 5;
        grid-column-end: 14;
        grid-row-start: 1;

        @include media('<=tablet') {
            display: none;
        }
    }

    .navigation--secondary  {
        grid-column-start: 6;
        grid-column-end: 14;
        grid-row-start: 2;

        @include media('<=tablet') {
            display: none;
        }
    }

    // mobile navigation menu
    .menu {
        grid-column-end: 25;
        grid-column-start: 14;

        @include media('>tablet') {
            display: none;
        }
    }
}

.global-header__content {
    display: grid;
    border-bottom: $border-width-thick $border-style-solid $color-raisin-black;
    padding-bottom: $measure--small2;
    @include default-padding();
    @include x-column-grid(24);
}

.global-header__logo {
    color: $color-purewhite;
    font-weight: $font-weight-book;
    letter-spacing: -0.5px;
    font-size: $measure--medium1;
    font-family: "ff-meta-web",Arial,Helvetica,sans-serif;
    display: inherit;
    grid-column-start: 1;
    grid-column-end: 5;
    
    

    &:visited {
        color: $color-purewhite;
    }
    
    &:focus, &:active {
        //border: 1px dotted #999999;
    }

    span.global-header__logo-style {
        font-weight: $font-weight-bold;
        letter-spacing: -0.5px;
    }
}

.global-header__membership-ctas {
    grid-column-end: 25;
    grid-column-start: 14;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;

    @include media('<=tablet') {
        grid-row-start: 2;
        grid-column-start: 1;
        grid-column-end: 13;
    }


    .global-header__membership-cta:first-of-type {
        margin-right: $measure--basic;
    }
}
